body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.allCards {
    padding: 10px;
    margin-bottom: 10px;
    border: none;
    text-align: center;
}

.allCards img {
    margin: 5px;
    border: 5px solid blueviolet;
    max-height: 300px;
    max-width: 200px;
    
}
.mainCardContainer {
    /* margin-bottom: 50px; */
    padding: 0; 
    background-image: url("https://images.pexels.com/photos/110854/pexels-photo-110854.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");
    background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}

.gameMessage {
    text-align: center;
    max-width: 100%;
    -webkit-align-content: center;
            align-content: center
}

.gameMessage p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color: black;
    color: white;
    padding: 20px 0;
    margin-bottom: 0px;
}

.gameScores {
    text-align: center;
    max-width: 100%;
    -webkit-align-content: center;
            align-content: center;
    margin-bottom: 10px;
}
.gameScores p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color: blueviolet;
    color: white;
    padding: 20px 0;
    margin-bottom: 25px;
}
.jumbotron {
	background-image: url("https://i.redd.it/kg7dlnm296t01.jpg");
    /* background-blend-mode: lighten; */
    background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	margin-bottom: 0px;
    text-align: center;
}

@font-face {
    font-family: 'Bank Gothic Medium';
    font-style: normal;
    font-weight: normal;
    src: local('Bank Gothic Medium'), url('/src/fonts/ufonts.com_bank-gothic-medium.woff') format('woff');
    }
  

.jumbotron h1 {
	font-family: 'Montserrat', sans-serif;
	font-size: 60px;
	text-transform: uppercase;
	font-weight: 900;
    color: blueviolet;
    text-shadow: 2px 2px 4px black;
    text-align: right;
}

.jumbotron p {
	font-family: 'Montserrat', sans-serif;
	font-size: 18px;
	font-weight: 500;
    text-transform: uppercase;
    color: white;
    text-align: right;
}

.mainContainer {
  padding: 0; 
  background-color: rgba(0, 0, 0, 0.03);
  min-height: 100vh;
  /* padding-bottom: 100px; */
  }

